import React, { useState, useEffect } from 'react';
import './PreSurvey.css';
import { db } from './firebase';
import { doc, setDoc } from 'firebase/firestore';
import { useParams, useNavigate } from 'react-router-dom';
import { gsap } from 'gsap';

// --- Survey Structure ---
const surveySections = [
  {
    title: ' 1: ',
    questions: [
      { id: 's1_q1', text: ' ' }, // 
      { id: 's1_q2', text: '' }, // 
      { id: 's1_q3', text: '' }, // 
      { id: 's1_q4', text: ' ' }, // 
      { id: 's1_q5', text: '' }, // 
    ]
  },
  {
    title: ' 2:  (Self-awareness)',
    questions: [
      { id: 's2_q1', text: ' ' }, // 
      { id: 's2_q2', text: '' }, // 
      { id: 's2_q3', text: '' }, // 
      { id: 's2_q4', text: '' }, // 
    ]
  },
  {
    title: ' 3:  (Self-regulation)',
    questions: [
      { id: 's3_q1', text: ' ' }, // 
      { id: 's3_q2', text: '' }, // 
      { id: 's3_q3', text: ' ' }, // 
    ]
  },
  {
    title: ' 4:  (Motivation)',
    questions: [
      { id: 's4_q1', text: '' }, // 
      { id: 's4_q2', text: '' }, // 
      { id: 's4_q3', text: ' ' }, // 
    ]
  },
  {
    title: ' 5:  (Empathy)',
    questions: [
      { id: 's5_q1', text: '' }, // 
      { id: 's5_q2', text: ' \'\'  ( )' }, // 
      { id: 's5_q3', text: '' }, // 
    ]
  },
  {
    title: ' 6:  (Social Skills)',
    questions: [
      { id: 's6_q1', text: '' }, // 
      { id: 's6_q2', text: '' }, // 
      { id: 's6_q3', text: '' }, // 
    ]
  },
  {
    title: ' 7: ',
    questions: [
      { id: 's7_q1', text: '' }, // 
      { id: 's7_q2', text: '' }, // 
    ]
  }
];

// Generate initial state from the survey structure
const initialResponses = surveySections.flatMap(section => section.questions).reduce((acc, question) => {
  acc[question.id] = '';
  return acc;
}, {});

const ratingOptions = [
  { value: '5', label: '5' },
  { value: '4', label: '4' },
  { value: '3', label: '3' },
  { value: '2', label: '2' },
  { value: '1', label: '1' },
];

function PreSurvey() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [responses, setResponses] = useState(initialResponses);

  useEffect(() => {
    gsap.fromTo('.presurvey-container', { opacity: 0, y: 20 }, { opacity: 1, y: 0, duration: 1, ease: 'power3.out' });
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setResponses(prevResponses => ({
      ...prevResponses,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    //  form  ()
    const formData = JSON.parse(localStorage.getItem('formData') || '{}');
    const combinedData = {
      ...formData,
      presurvey: responses,
      timestamp: new Date().toISOString()
    };
    
    //  localStorage 
    localStorage.setItem('presurveyData', JSON.stringify(responses));
    localStorage.setItem('userData', JSON.stringify(combinedData));
    
    // 
    if (formData.name) {
      localStorage.setItem('playerName', formData.name);
    }
    
    console.log('Saved presurvey to localStorage:', responses);
    console.log('Combined userData saved:', combinedData);
    
    try {
      //  Firebase
      await setDoc(doc(db, "formdata", id), { presurvey: responses }, { merge: true });
      console.log("Pre-survey data added to document with ID: ", id);
    } catch (error) {
      console.error("Error adding document to Firebase: ", error);
      console.log("Continuing with localStorage data...");
    }
    
    gsap.to('.presurvey-container', {
      opacity: 0,
      y: -20,
      duration: 0.7,
      ease: 'power3.in',
      onComplete: () => {
        navigate(`/quote/${id}`);
      }
    });
  };

  return (
    <div style={{ 
      height: '100vh', 
      overflowY: 'auto', 
      overflowX: 'hidden',
      backgroundColor: '#fdf2f8'
    }}>
      <div className="presurvey-container">
        <div className="presurvey-header">
          <h2> '': </h2>
          <div className="instructions">
          <p><strong>:</strong> "  ''   "</p>
        </div>
        <div className="scale-guide">
          <p><strong>:</strong> 1 = , 2 = , 3 = , 4 = , 5 = </p>
        </div>
      </div>

      <form onSubmit={handleSubmit}>
        {surveySections.map((section) => (
          <div className="survey-section" key={section.title}>
            <h3>{section.title}</h3>
            {section.questions.map((question) => (
              <div className="presurvey-field" key={question.id}>
                <label className="question-text">{question.text}</label>
                <div className="rating-scale-container">
                  <span className="scale-label-start"></span>
                  <div className="options">
                    {ratingOptions.map((option) => (
                      <label key={option.value} className="option-label">
                        <input
                          type="radio"
                          name={question.id}
                          value={option.value}
                          checked={responses[question.id] === option.value}
                          onChange={handleChange}
                          required
                        />
                        <span className="option-value">{option.label}</span>
                      </label>
                    ))}
                  </div>
                  <span className="scale-label-end"></span>
                </div>
              </div>
            ))}
          </div>
        ))}
        <button type="submit"></button>
      </form>
      </div>
    </div>
  );
}

export default PreSurvey;