import React, { useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import gsap from 'gsap';

const Caution = () => {
  const navigate = useNavigate();
  const cautionRef = useRef(null);

  useEffect(() => {
    gsap.fromTo(cautionRef.current,
      { opacity: 0 },
      {
        opacity: 1,
        duration: 2.5,
        ease: "power2.inOut"
      }
    );
  }, []);

  const handleClick = () => {
    gsap.to(cautionRef.current, {
      opacity: 0,
      duration: 1.5,
      ease: "power2.inOut",
      onComplete: () => navigate('/logo')
    });
  };

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center bg-white p-4"
      onClick={handleClick}
      ref={cautionRef}
    >
      {/*  inline styles  */}
      <div 
        className="w-full max-w-sm text-center px-6"
        style={{
          fontSize: '16px', //  12px  16px
          lineHeight: '1.4'
        }}
      >
        <h2 
          className="mb-4 text-2xl font-bold text-red-500"
          style={{
            fontSize: '24px', //  18px  24px
            marginBottom: '16px'
          }}
        >
          
        </h2>

        {/*  */}
        <p 
          className="mb-3 text-base text-gray-700 text-balance"
          style={{
            fontSize: '16px', //  10px  14px
            marginBottom: '12px',
            lineHeight: '1.5'
          }}
        >
                
        </p>
        <p 
          className="mb-3 text-base text-gray-700 text-balance"
          style={{
            fontSize: '16px', //  10px  14px
            marginBottom: '12px',
            lineHeight: '1.5'
          }}
        >
          
           
        </p>
        <p 
          className="mb-3 text-base text-gray-700 text-balance"
          style={{
            fontSize: '14px', //  10px  14px
            marginBottom: '12px',
            lineHeight: '1.5'
          }}
        >
            
        </p>
        <p 
          className="mb-3 text-base text-gray-700"
          style={{
            fontSize: '15px', //  11px  15px
            marginBottom: '12px',
            fontWeight: 'bold',
            lineHeight: '1.4'
          }}
        >
          
        </p>
        
        <p 
          className="mt-6 text-xs italic text-gray-600"
          style={{
            fontSize: '14px', //  8px  11px
            marginTop: '16px',
            lineHeight: '1.4',
            fontStyle: 'italic'
          }}
        >
          *
           
           
           
          <br />  
          <br />
        </p>
      </div>
    </div>
  );
};

export default Caution;