import React, { useState, useEffect } from 'react';
import './PostSurvey.css'; //  CSS 
import { db } from './firebase';
import { doc, setDoc } from 'firebase/firestore';
import { useParams, useNavigate } from 'react-router-dom'; //  useNavigate
import { gsap } from 'gsap';

const surveySections = [
  {
    title: ' 1: ',
    questions: [
      { id: 'posts_s1_q1', text: ' ' },
      { id: 'posts_s1_q2', text: '' },
      { id: 'posts_s1_q3', text: '' },
      { id: 'posts_s1_q4', text: ' ' },
      { id: 'posts_s1_q5', text: '' },
    ]
  },
  {
    title: ' 2:  (Self-awareness)',
    questions: [
      { id: 'posts_s2_q1', text: ' ' },
      { id: 'posts_s2_q2', text: '' },
      { id: 'posts_s2_q3', text: '' },
      { id: 'posts_s2_q4', text: '' },
    ]
  },
  {
    title: ' 3:  (Self-regulation)',
    questions: [
      { id: 'posts_s3_q1', text: ' ' },
      { id: 'posts_s3_q2', text: '' },
      { id: 'posts_s3_q3', text: ' ' },
    ]
  },
  {
    title: ' 4:  (Motivation)',
    questions: [
      { id: 'posts_s4_q1', text: '' },
      { id: 'posts_s4_q2', text: '' },
      { id: 'posts_s4_q3', text: ' ' },
    ]
  },
  {
    title: ' 5:  (Empathy)',
    questions: [
      { id: 'posts_s5_q1', text: '' },
      { id: 'posts_s5_q2', text: ' \'\'  ( )' },
      { id: 'posts_s5_q3', text: '' },
    ]
  },
  {
    title: ' 6:  (Social Skills)',
    questions: [
      { id: 'posts_s6_q1', text: '' },
      { id: 'posts_s6_q2', text: '' },
      { id: 'posts_s6_q3', text: '' },
    ]
  },
  {
    title: ' 7: ',
    questions: [
      { id: 'posts_s7_q1', text: ' \'\' ' },
      { id: 'posts_s7_q2', text: '' },
      { id: 'posts_s7_q3', text: ' ' },
      { id: 'posts_s7_q4', text: '' },
    ]
  }
];

const initialResponses = surveySections.flatMap(section => section.questions).reduce((acc, question) => {
  acc[question.id] = '';
  return acc;
}, { feedback: '' }); //  state 

const ratingOptions = [
  { value: '5', label: '5' },
  { value: '4', label: '4' },
  { value: '3', label: '3' },
  { value: '2', label: '2' },
  { value: '1', label: '1' },
];

function PostSurvey() {
  const { id } = useParams();
  const navigate = useNavigate(); // 
  const [responses, setResponses] = useState(initialResponses);

  useEffect(() => {
    gsap.fromTo('.postsurvey-container', { opacity: 0 }, { opacity: 1, duration: 1 });
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setResponses(prevResponses => ({
      ...prevResponses,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("PostSurvey handleSubmit called");
    
    try {
      await setDoc(doc(db, "formdata", id), { postsurvey: responses }, { merge: true });
      console.log("Post-survey data added to document with ID: ", id);
      
      //  localStorage  Story.js 
      const userCardData = localStorage.getItem('userCardData');
      console.log("Retrieved userCardData from localStorage:", userCardData);
      
      let cardParams = '';
      
      if (userCardData) {
        const cardData = JSON.parse(userCardData);
        console.log("Parsed cardData:", cardData);
        cardParams = `?cardType=${encodeURIComponent(cardData.cardType)}&cardTitle=${encodeURIComponent(cardData.cardTitle)}&cardImage=${encodeURIComponent(cardData.cardImage)}`;
        //  localStorage 
        localStorage.removeItem('userCardData');
      } else {
        console.log("No userCardData found, using fallback");
        // fallback 
        cardParams = `?cardType=&cardTitle=&cardImage=`;
      }
      
      const targetUrl = `/resultcard/${id}${cardParams}`;
      console.log("Navigating to:", targetUrl);
      
      //  window.location  navigate 
      gsap.to('.postsurvey-container', {
        opacity: 0,
        y: -20,
        duration: 0.7,
        ease: 'power3.in',
        onComplete: () => {
          navigate(targetUrl); //  navigate  window.location
        }
      });
      
    } catch (error) {
      console.error("Error adding document: ", error);
    }
  };

  return (
    <div style={{ 
      height: '100vh', 
      overflowY: 'auto', 
      overflowX: 'hidden',
      backgroundColor: '#f5f3ff'
    }}>
      <div className="postsurvey-container">
        <div className="postsurvey-header">
          <h2>: </h2>
          <div className="instructions">
            <p><strong>:</strong> "!    '' ()  "</p>
          </div>
        <div className="scale-guide">
          <p><strong>:</strong> 5 = , 4 = , 3 = , 2 = , 1 = </p>
        </div>
      </div>

      <form onSubmit={handleSubmit}>
        {surveySections.map((section) => (
          <div className="survey-section" key={section.title}>
            <h3>{section.title}</h3>
            {section.questions.map((question) => (
              <div className="postsurvey-field" key={question.id}>
                <label className="question-text">{question.text}</label>
                <div className="rating-scale-container">
                  <span className="scale-label-start"></span>
                  <div className="options">
                    {ratingOptions.map((option) => (
                      <label key={option.value} className="option-label">
                        <input
                          type="radio"
                          name={question.id}
                          value={option.value}
                          checked={responses[question.id] === option.value}
                          onChange={handleChange}
                          required
                        />
                        <span className="option-value">{option.label}</span>
                      </label>
                    ))}
                  </div>
                  <span className="scale-label-end"></span>
                </div>
              </div>
            ))}
          </div>
        ))}

        <div className="survey-section">
           <h3></h3>
           <div className="postsurvey-field">
            <label>? ( , , , , )</label>
            <textarea
              name="feedback"
              value={responses.feedback}
              onChange={handleChange}
              placeholder="..."
            />
          </div>
        </div>

        <button type="submit"></button>
      </form>
      </div>
    </div>
  );
}

export default PostSurvey;